import axios from "axios";
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import Button from '@mui/material/Button';
import ReactImageMagnify from 'react-image-magnify';
import { useDispatch } from "react-redux";
import { addToCard } from "../../store/slices/cardSlice";
import toast, { Toaster } from 'react-hot-toast';




export const Productdetail = () => {
    const dispatch=useDispatch()
    const [state, setState] = useState({

        title: '',
        price: '',
        category: '',
        description: '',
        image: ''
    }
    )
    let { id } = useParams();

    useEffect(() => {
        axios.get("https://fakestoreapi.com/products/" + id)
            .then((res) => {
                // console.log(res);
                setState(res.data)
            })
    }, [])
    const addToCaradData=(product)=>{
            // console.log(product);
            dispatch(addToCard(product))
            toast.success('Cart Add SucessfullY !');
    }
    console.log(id);
    return (
        <>
          <Toaster />
            <div className="container">
                <div className="row">
                    <div className="col-md-12">
                        <div class="card mb-3">
                            <div class="row g-0">
                                <div class="col-md-6">
                                    {/* <img src={state.image} style={{width:"100%",height:"550px"}} class="img-fluid rounded-start" alt="..." /> */}
                                    <div style={{ width: "500px" }}>
                                        <ReactImageMagnify {...{
                                            smallImage: {
                                                alt: 'Wristwatch by Ted Baker London',
                                                // isFluidWidth: true,

                                                src: state.image,
                                                width: 550,
                                                height: 462
                                            },
                                            largeImage: {
                                                src: state.image,
                                                width: 800,
                                                height: 1100
                                            }
                                        }} />
                                    </div>
                                </div>
                                <div class="col-md-6">
                                    <div class="card-body">
                                        <h5 class="card-title">{state.title}</h5>
                                        <p class="card-text" style={{textAlign:"justify"}}>{state.description}</p>
                                        <p class="card-text"><small class="text-body-secondary">Last updated 3 mins ago</small></p>
                                        <br />
                                        <br />
                                        {/* <Button variant="contained" size="medium" >Add to card</Button> */}
                                        <button  class="btn btn-danger btn-sm" onClick={()=>{addToCaradData(state)}} >Add to Card</button>

                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </>
    )
}
