import React, { useState } from 'react'
import './sign_up.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import toast, { Toaster } from 'react-hot-toast';


const Signup = () => {
    const [state,setState]=useState({
        f_name:"",
        l_name:"",
        email_id:"",
        mob_num:"",
        password:""
    })

    const handler=(event)=>{
        const {name,value}=event.target;
        setState({...state,[name]:value})
    }
    const signUp=(event)=>
    {
        event.preventDefault()
        // console.log(state);
        axios.post("http://localhost:4004/user-details",state)
        .then((res)=>{
          console.log(res);
          //  toast.success('Cart Add SucessfullY !');
          if(res.data.success===true)
          {
             toast.success(res.data.message);
          }
          else 
          {
            toast.success(res.data.message);
          }
        })

    }
  return (
   <div className="container">
    <div className="row">
        <div className="col-md-3"> <Toaster /></div>
        <div className="col-md-6">
        <div className="wrapper">
  <h2>Registration</h2>
  <form action="#" onSubmit={signUp}>
    <div className="input-box">
      <input type="text" placeholder="Enter your first name" name="f_name" onChange={handler} />
    </div>
    <div className="input-box">
      <input type="text" placeholder="Enter your last name" name="l_name" onChange={handler} />
    </div>
    <div className="input-box">
      <input type="text" placeholder="Enter your email id" name="email_id" onChange={handler} />
    </div>
    <div className="input-box">
      <input type="text" placeholder="Enter your mobile number" name="mob_num" onChange={handler} />
    </div>
    <div className="input-box">
      <input type="password" placeholder="Create password" name="password" onChange={handler} />
    </div>

    <div className="policy">
      <input type="checkbox" />
      <h3>I accept all terms &amp; condition</h3>
    </div>
    <div className="input-box button">
      <input type="Submit" defaultValue="Register Now" />
    </div>
    <div className="text">
      <h3>
        Already have an account? <Link to="/login">Login now</Link>
      </h3>
    </div>
  </form>
</div>
        </div>
        <div className="col-md-3"></div>
    </div>
   </div>

  )
}

export default Signup