
import axios from "axios";
import React, { useEffect, useState } from 'react'
import { Link, useNavigate, useSearchParams } from "react-router-dom";
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import PacmanLoader from "react-spinners/PacmanLoader";
import { useDispatch } from "react-redux";
import { addToCard } from "../../store/slices/cardSlice";
import toast, { Toaster } from 'react-hot-toast';
export const Dashboard = () => {
    const [state, setState] = useState([]);
    const [cate, setCate] = useState([])
    const _useNavigate=useNavigate()
    const [searchParams] = useSearchParams();
    const query = searchParams.get('cate_name');
    const [loading, setLoading] = useState(false)
    const dispatch=useDispatch()
    // alert(query)

    const gelAllCate = () => {
        axios.get("https://fakestoreapi.com/products/categories")
            .then((res) => {
                setCate(res.data)
            })
    }

    const callAllCateData = (categoery) => {
        console.log(categoery);
        axios.get("https://fakestoreapi.com/products/category/" + categoery)
            .then((res) => {
                // console.log(res);
                setState(res.data)
            })
    }
    const getAllProductData = () => {
        axios.get("https://fakestoreapi.com/products")
            .then((res) => {
                // console.log(res);
                setState(res.data)
            })
    }
    useEffect(() => {
       
        if(query!=null)
        {
            getAllProductData()
            gelAllCate()
            callAllCateData(query)
           
        }
        else{
            getAllProductData()
            gelAllCate()
        }
    }, [query])


    /* for react sinper */
    useEffect(() => {
        setLoading(true)
        setTimeout(() => {
            setLoading(false)
        }, 1000)
    }, [])

    const gotoProdcutDetail=(id)=>{
        _useNavigate(`product-detail/${id}`)

    }

    const addToCardData=(product)=>{
        dispatch(addToCard(product))
        toast.success('Cart Add SucessfullY !');
    }
    return (
        <>
         <Toaster />
        {
            loading ?<div style={{ marginLeft: "48%", marginTop: "20%", marginBottom: "30%" }}>
                    <PacmanLoader color={'#49be25'} loading={loading} size={20} />
                    </div>:
                    <div className="container-fluid">
                <div className="row">
                    <div className="col-md-2">
                        <ul class="list-group">
                            <li class="list-group-item active" aria-current="true">Categoery</li>
                            {
                                cate.map((data, index) =>
                                    <li class="list-group-item" aria-current="true">
                                        {/* <Link  to="" sty>{data}</Link> */}
                                        <a href="#" style={{ textDecoration: "none", fontSize: "18px", textTransform: "capitalize" }} onClick={() => { callAllCateData(data) }}>{data}</a>
                                    </li>
                                )
                            }

                        </ul>
                    </div>
                    <div className="col-md-10">
                        <div className="container-fluid">
                            <div className="row">
                                {
                                    state.map((item, index) =>
                                    <div className="col-md-3" style={{ padding: "5px" }}>
                                        <Card
                                            raised
                                            sx={{
                                                maxWidth: 300,
                                                margin: "0 auto",
                                                padding: "0.1em",
                                            }}
                                        >
                                            <CardMedia
                                                component="img"
                                                alt="green iguana"
                                                height="200"
                                                image={item.image}
                                                sx={{ padding: "1em 1em 0 1em", objectFit: "contain" }}
                                            />
                                            <CardContent>
                                                <Typography gutterBottom variant="h5" component="div" sx={{fontSize:"20px"}}>
                                                   {item.title.substring(0,20)}
                                                </Typography>
                                                <Typography variant="body2" color="text.secondary">
                                                   {item.description.substring(0,60)}
                                                </Typography>
                                            </CardContent>
                                            <CardActions>
                                                {/* <Button  variant="contained" size="medium" cl>Add to card</Button> */}
                                                <input type="submit" value="Add To Card" className="btn btn-success" onClick={()=>{addToCardData(item)}} />
                                               
                                               <span style={{marginLeft:"28%"}}>
                                                    <Button variant="contained" size="medium" onClick={()=>{gotoProdcutDetail(item.id)}}>View</Button>
                                               </span> 
                                            </CardActions>
                                        </Card>
                                        </div>
                                    )
                                }


                            </div>
                        </div>
                    </div>
                </div>
            </div>
        }
           
        </>
    )
}
