import React, { useState } from 'react'
import './login.css';
import { Link, useNavigate } from 'react-router-dom';
import axios from 'axios';


export const Login = () => {
  const [state,setState]=useState({
    username:'',
    password:''
  })
  const _useNavigate=useNavigate()
  const handler=(event)=>{
    const {name,value}=event.target 
    setState({...state,[name]:value})
  }
  const loginForm=(event)=>{
    event.preventDefault()
    axios.post("http://localhost:4004/login",state).then((res)=>{
      // console.log(res);
      if(res.data.success==true)
      {
        localStorage.setItem("_token",res.data.token)
        _useNavigate("/")

      }
    })
  }
  
  return (
    <div className="container">
      <div className="row">
        <div className="col-md-3"></div>
        <div className="col-md-6">
          <div className="wrapper">
            <h2>Login Here !</h2>
            <form action="#" onSubmit={loginForm} method='post'>

              <div className="input-box">
                <input type="text" name='username' onChange={handler} placeholder="Enter your email" required="" />
              </div>
              <div className="input-box">
                <input type="text" name='password' onChange={handler}  placeholder="Create password" required="" />
              </div>


              <div className="input-box button">
                <input type="Submit" defaultValue="Register Now" />
              </div>
              <div className="text">
                <h3>
                  Already have an account? <Link to="/sign-up">Sign now</Link>
                </h3>
              </div>
            </form>
          </div>
        </div>
        <div className="col-md-3"></div>
      </div>
    </div>
  )
}
